<template>
	<v-container>
		<v-row align="center" justify="center" class="word-bar" v-if="!loading">
			<v-chip color="primary" link to="/user" style="font-size: large">{{
				selection ? selection.word : "..."
			}}</v-chip>
		</v-row>
		<v-container style="padding-top: 15px">
			<loading v-if="loading" />
			<v-container v-else-if="selection.selected">
				<v-img
					class="my-3"
					contain
					:src="selection.selected.original"
					max-height="400px"
				/>
				{{ selection.selected.title }}
				<br />
				<div style="color: green">
					From seller: {{ selection.selected.user }}
				</div>
			</v-container>

			<v-row
				v-else-if="!Object.entries(pictures).length"
				class="fill-height"
				align-content="center"
				justify="center"
				style="margin-top: 20%"
			>
				<v-col class="text-subtitle-1 text-center" cols="12">
					<h3>
						Awaiting proposals ...<router-link to="/user">
							go back.</router-link
						>
					</h3>
					<i style="font-size: small"
						>No user has proposed any image yet</i
					>
				</v-col>
			</v-row>
			<v-container v-else-if="thumbnailView">
				<!-- <h2>User Select View</h2>
		{{ selection }}
    -->

				<v-row align-content="center" justify="center">
					<v-col
						v-for="(pict, index) in pictures"
						:key="index"
						class="pict-card"
					>
						<tooltip :text="pict.title">
							<div class="img-container">
								<v-img
									class="my-3 thumbnail"
									contain
									:src="pict.thumbnail"
									max-height="200px"
									@click="() => showCarousel(index)"
								/>
								<!-- <v-checkbox
							color="primary"
							class="img-chckbx"
							:value="selected === index"
							@click="() => toggleSelected(index)"
						/> -->
							</div>
						</tooltip>
						<!-- <div style="font-size:x-small">{{ pict.title }}</div> -->
					</v-col>
				</v-row>
			</v-container>
			<div class="carousel-container" v-else>
				<v-carousel
					v-model="carousel"
					hide-delimiters
					show-arrows-on-hover
					hide-delimiter-background
				>
					<div
						v-for="(pict, index) in pictures"
						:key="index"
						:text="pict.title"
					>
						<v-carousel-item>
							<v-sheet height="100%" class="transparent-sheet">
								<v-row
									class="fill-height"
									align="center"
									justify="center"
								>
									<v-img
										class="my-3"
										contain
										:src="pict.original"
										max-height="100%"
									>
										<template v-slot:placeholder>
											<v-img
												class="my-3"
												contain
												:src="pict.thumbnail"
												max-height="100%"
											>
												<template v-slot:placeholder>
													<v-row
														class="fill-height ma-0"
														align="center"
														justify="center"
													>
														<v-progress-circular
															indeterminate
															color="primary lighten-5"
														></v-progress-circular>
													</v-row>
												</template>
											</v-img>
										</template>
									</v-img>
								</v-row>
							</v-sheet>
						</v-carousel-item>
					</div>
				</v-carousel>
				<v-row class="actions-row">
					<tooltip text="Select current image">
						<v-btn
							fab
							class="action-btn"
							small
							color="primary"
							@click="confirmDialog = true"
						>
							<v-icon> mdi-check </v-icon>
						</v-btn>
					</tooltip>
					<tooltip text="Close preview">
						<v-btn
							fab
							class="action-btn"
							small
							color="primary"
							@click="thumbnailView = true"
						>
							<v-icon> mdi-close </v-icon>
						</v-btn>
					</tooltip>
				</v-row>
				<confirm-dialog
					:text="`Once you select the image there's no going back, the corresponding seller will recive the points and the quiz related to this word (${selection.word}) would be closed`"
					v-model="confirmDialog"
					@accept="() => select(carousel)"
				/>
			</div>
		</v-container>
	</v-container>
</template>

<script>
import Loading from "@/common/components/Loading.vue";
import { mapActions, mapState } from "vuex";
import Tooltip from "@/common/components/Tooltip.vue";
import { closeSelection } from "../services/firebaseServices";
import ConfirmDialog from "@/common/components/ConfirmDialog.vue";

export default {
	components: { Loading, Tooltip, ConfirmDialog },
	data() {
		return {
			selection: null,
			selected: -1,
			thumbnailView: true,
			carousel: 0,
			confirmDialog: false,
			id: -1,
		};
	},
	computed: {
		...mapState("user", ["currentRace"]),
		loading() {
			return !Boolean(this.currentRace);
		},
		pictures() {
			return Object.entries(this.selection.pictures).map((p) => ({
				...p[1],
				user: p[0],
			}));
		},
	},
	watch: {
		loading: {
			immediate: true,
			handler(val) {
				if (val) return;
				let id = +this.$router.currentRoute.params.idx - 1;
				console.log({ id });
				if (
					Number.isNaN(id) ||
					id >= this.currentRace.length ||
					id < 0
				) {
					this.$toast.error("No se encuentra la ruta solicitada");
					this.$router.push("/404");
				}
				this.id = id;
				this.selection = this.currentRace[id];
				this.setWindowTitle('"' + this.selection.word + '" quiz');
			},
		},
	},
	methods: {
		...mapActions("user", ["setWindowTitle"]),
		toggleSelected(i) {
			if (this.selected === i) {
				this.selected = null;
			} else {
				this.selected = i;
			}
		},
		showCarousel(index) {
			this.thumbnailView = false;
			this.carousel = index;
		},
		async select(idx) {
			try {
				await closeSelection(this.id, this.pictures[idx]);
				this.$toast.success("Image successfully selected");
				this.$toast.success("+3pts to " + idx);
				// this.$router.push("/user");
			} catch (e) {
				this.$toast.error(e.message);
			}
		},
	},
};
</script>

<style>
.pict-card {
	background-color: transparent;
	/* display: flex; */
}
.thumbnail {
	cursor: pointer;
}
.transparent-sheet {
	background-color: transparent !important;
}
.carousel-container {
	position: relative;
}
.actions-row {
	position: absolute;
	top: -17px;
	right: 10px;
}
.action-btn {
	margin: 5px;
	z-index: 2;
}
.word-bar {
	position: fixed;
	z-index: 2;
	width: 100%;
	margin: 0;
	left: 0px;
}
</style>
